.person-top {
    margin-bottom: 0.24rem;
}

.person-head {
    padding: 0.21rem 0 0.4rem 0.24rem;
    height: 1.16rem;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;

}

.person-head>div:last-child {
    width: 4.6rem;
}

.person-head>div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.person-head>div div:nth-child(1) {
    font-size: 0.14rem;
    color: #909cb1;
}

.person-head>div div:nth-child(2) {
    font-size: 0.16rem;
    font-family: 'PFC';
}

.green-color {
    color: #16a944;
}

.mid-color {
    color: #999999;
}

.power-position {
    position: relative;
    padding: 0.2rem 0 0.2rem 0.38rem;
}

.power-item {
    width: 2.52rem;
    height: 1rem;
    box-sizing: border-box;
    background: #ffffff;
    border-radius: 6px;
    border: 1px solid #eff0f4;
    float: left;
    margin-bottom: 17px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.power-item:nth-child(1),
.power-item:nth-child(2) {
    margin-top: 36px;
}

.power-item:nth-child(2n+1) {
    margin-left: 29px;
}

.power-item:nth-child(2n) {
    margin-left: 24px;
}

.powerBule::after {
    content: "";
    position: absolute;
    left: -0.03rem;
    top: 0.39rem;
    width: 0.05rem;
    height: 0.14rem;
    background: #7486f9;
    border-radius: 3px;
}

.powerYellow::after {
    content: "";
    position: absolute;
    left: -0.03rem;
    top: 0.39rem;
    width: 0.05rem;
    height: 0.14rem;
    background: #ff8c56;
    border-radius: 3px;
}

.powerViolet::after {
    content: "";
    position: absolute;
    left: -0.03rem;
    top: 0.39rem;
    width: 0.05rem;
    height: 0.14rem;
    background: #713bdb;
    border-radius: 3px;
}

.powerRed::after {
    content: "";
    position: absolute;
    left: -0.03rem;
    top: 0.39rem;
    width: 0.05rem;
    height: 0.14rem;
    background: #f0527e;
    border-radius: 3px;
}

.trendChart {
    width: 100%;
    height: 100%;
}

.rankChart {
    width: 100%;
    height: 100%;
}

.rankBtns {
    position: absolute;
    font-size: 0.14rem;
    top: 0.25rem;
    left: 0.24rem;
    right: 0;
    box-sizing: border-box;
}

.rankBtns span {
    margin-right: 0.08rem;
    padding: 0 0.12rem;
    font-size: 0.14rem;
    line-height: 0.2rem;
    color: #909cb1;
    border: 1px solid #909cb1;
    border-radius: 0.04rem;
    cursor: pointer;
}

.rankChecked {
    border: 1px solid #2e4af7 !important;
    background: rgba(46, 74, 247, 0.12);
    color: #2e4af7 !important;
}
